@import url(https://fonts.googleapis.com/css?family=Rancho&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  font-family: "Rancho", cursive;
}

.weather-card {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 300px;
  height: 380px;
  text-align: left;
  color: #ccc;
}
.weather-card-info {
  font-size: 26px;
  padding: 0;
}
.weather-card-image {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 120px;
  height: 120px;
}
.weather-icon-image {
  width: 100px;
  height: 100px;
}

body {
  background-color: #000;
  color: #ccc;
}
.form-component {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 0 auto;
  width: 350px;
  height: 100%;
  border-radius: 25px;
}
.choice-of-service {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: left;
  width: 300px;
  height: 100px;
  margin-top: 20px;
}
input[type="radio"] {
  background-color: transparent;
  border: 0.0625em solid rgba(255, 255, 255, 0.5);
  border-radius: 50%;
  box-shadow: inset 0 0 0 0 white;
  cursor: pointer;
  font: inherit;
  height: 1em;
  outline: none;
  width: 1em;
  -webkit-appearance: none;
  cursor: pointer;
}
input:checked {
  background-color: #fae607;
  box-shadow: inset 0 0 0 0.1875em #fae607;
  -webkit-transition: background 0.15s, box-shadow 0.1s;
  transition: background 0.15s, box-shadow 0.1s;
}
input[type="radio"]:checked ~ label {
  color: #fae607;
}
::-webkit-input-placeholder {
  font-size: 20px;
}
.input-label {
  cursor: pointer;
  font-size: 26px;
}
.search-btn {
  background-color: #fae607;
  color: #1d1d1d;
  padding: 10px 22px;
  border-radius: 0 10px 10px 0;
  border: 0 none;
  font-weight: bold;
  font-size: 22px;
  cursor: pointer;
}
.search-btn:focus {
  outline: 0;
}
#search-box {
  background: #eee;
  padding: 10px;
  border-radius: 10px 0 0 10px;
  border: 0 none;
  width: 200px;
  outline: none;
  cursor: pointer;
}
.service-input {
  padding: 10px 0;
}
.search-input {
  background: #eee;
  margin-top: 20px;
  box-shadow: 0 0 5px 2.5px #fae607;
  border-radius: 12px;
}

